.header {
    background-color: white;
    border-bottom: 1px solid #E5E9F2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 72px;
  }
  
  .logo {
    align-items: center;
    height: 72px;
    margin-right: 20px;
  }
  
  .logo img {
    width: 188px;
    height: 72px;
    object-fit: contain;
    padding-left: 72px;
  }
  
  .nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 165px;
  }
  
  .nav a {
    text-decoration: none;
    margin-left: 20px;
    color: #18191F;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
  }
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 72px;
    background-color: #0B0D17;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
  
  .left-section {
    font-size: 14px;
    color: #D9DBE1;
    padding-left: 165px;
    font-family: 'Inter', sans-serif;
  }
  
  .right-section {
    display: flex;
    justify-content: flex-end;
    padding-right: 165px;
  }
  
  .right-section a {
    color: white;
    margin-left: 10px;
    margin-right: 16px; /* Agrega la separación entre iconos */
  }
.main {
    height: calc(100vh - 144px);
    display: flex;
    justify-content: center;
    align-items: center;
}
.presentation {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

.presentation-button {
width: 235px;
height: 48px;
display: flex;
justify-content: center;
align-items: center;
background-color: #8C30F5;
color: white;
border-radius: 6px;
box-shadow: none;
border-width: 0px;
cursor: pointer;
font-family: 'Inter', sans-serif;
font-size: 20px;
}
  .presentation-container {
