.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    font-family: in;
  }
  
  .title {
    font-size: 48px;
    align-items: center;
    font-weight: 430;
    font-family: 'Inter', sans-serif;
