  .section1 > div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 0;
    flex-grow: 1;
    position: relative;
    display: inline-block;
    padding: 10px;
  }
  
  .section1 > div:first-child::before {
    display: none;
  }
  
  .section1 > div.active::before {
